//
// Buttons
// --------------------------------------------------


// Base styles
// --------------------------------------------------

.btn {
  height: 40px;
  display: inline-block;
  margin-bottom: 0; // For input.btn
  font-weight: @btn-font-weight;
  text-align: center;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  background-image: none; // Reset unusual Firefox-on-Android default style; see https://github.com/necolas/normalize.css/issues/214
  border: 1px solid transparent;
  white-space: nowrap;
  .button-size(@btn-default-padding-vertical; @btn-default-padding-horizontal; @font-size-base; @btn-default-line-height; @btn-border-radius-base);
  .user-select(none);

  &,
  &:active,
  &.active {
    &:focus,
    &.focus {
      .tab-focus();
    }
  }

  &:hover,
  &:focus,
  &.focus {
    color: @btn-default-color;
    text-decoration: none;
  }

  &:active,
  &.active {
    outline: 0;
    background-image: none;
    .box-shadow(inset 0 3px 5px rgba(0,0,0,.125));
  }

  &.disabled,
  &[disabled] {
    cursor: @cursor-disabled;
  }

  a& {
    &.disabled,
    fieldset[disabled] & {
      pointer-events: none; // Future-proof disabling of clicks on `<a>` elements
    }
  }
}


// Alternate buttons
// --------------------------------------------------

.btn-default {
  .button-variant-many(@btn-default-color; @btn-default-bg; @btn-default-border; @btn-default-shadow;
                      @btn-default-hover-color; @btn-default-hover-bg; @btn-default-hover-border; @btn-default-hover-shadow;
                      @btn-default-active-color; @btn-default-active-bg; @btn-default-active-border; @btn-default-active-shadow;
                      @btn-default-disabled-color; @btn-default-disabled-bg; @btn-default-disabled-border; @btn-default-disabled-shadow);
}
.btn-ghost {
  .button-variant-many(@btn-ghost-color; @btn-ghost-bg; @btn-ghost-border; @btn-ghost-shadow;
                      @btn-ghost-hover-color; @btn-ghost-hover-bg; @btn-ghost-hover-border; @btn-ghost-hover-shadow;
                      @btn-ghost-active-color; @btn-ghost-active-bg; @btn-ghost-active-border; @btn-ghost-active-shadow;
                      @btn-ghost-disabled-color; @btn-ghost-disabled-bg; @btn-ghost-disabled-border; @btn-ghost-disabled-shadow);
}
.btn-outline {
  .button-variant-many(@btn-outline-color; @btn-outline-bg; @btn-outline-border; @btn-outline-shadow;
                      @btn-outline-hover-color; @btn-outline-hover-bg; @btn-outline-hover-border; @btn-outline-hover-shadow;
                      @btn-outline-active-color; @btn-outline-active-bg; @btn-outline-active-border; @btn-outline-active-shadow;
                      @btn-outline-disabled-color; @btn-outline-disabled-bg; @btn-outline-disabled-border; @btn-outline-disabled-shadow);
}
.btn-solid {
  .button-variant-many(@btn-solid-color; @btn-solid-bg; @btn-solid-border; @btn-solid-shadow;
                      @btn-solid-hover-color; @btn-solid-hover-bg; @btn-solid-hover-border; @btn-solid-hover-shadow;
                      @btn-solid-active-color; @btn-solid-active-bg; @btn-solid-active-border; @btn-solid-active-shadow;
                      @btn-solid-disabled-color; @btn-solid-disabled-bg; @btn-solid-disabled-border; @btn-solid-disabled-shadow);
}
.btn-primary {
  .button-variant(@btn-primary-color; @btn-primary-bg; @btn-primary-border);
}
// Success appears as green
.btn-success {
  .button-variant(@btn-success-color; @btn-success-bg; @btn-success-border);
}
// Info appears as blue-green
.btn-info {
  .button-variant(@btn-info-color; @btn-info-bg; @btn-info-border);
}
// Warning appears as orange
.btn-warning {
  .button-variant(@btn-warning-color; @btn-warning-bg; @btn-warning-border);
}
// Danger and error appear as red
.btn-danger {
  .button-variant(@btn-danger-color; @btn-danger-bg; @btn-danger-border);
}


// Link buttons
// -------------------------

// Make a button look and behave like a link
.btn-link {
  color: @link-color;
  font-weight: normal;
  border-radius: 0;

  &,
  &:active,
  &.active,
  &[disabled],
  fieldset[disabled] & {
    background-color: transparent;
    .box-shadow(none);
  }
  &,
  &:hover,
  &:focus,
  &:active {
    border-color: transparent;
  }
  &:hover,
  &:focus {
    color: @link-hover-color;
    text-decoration: @link-hover-decoration;
    background-color: transparent;
  }
  &[disabled],
  fieldset[disabled] & {
    &:hover,
    &:focus {
      color: @btn-link-disabled-color;
      text-decoration: none;
    }
  }
}


// Button Sizes
// --------------------------------------------------
.btn-lg {
  // line-height: ensure even-numbered height of button next to large input
  .button-size(@btn-lg-padding-vertical; @btn-lg-padding-horizontal; @btn-lg-font-size; @btn-lg-line-height; @btn-lg-border-radius);
}
.btn-sm {
  // line-height: ensure proper height of button next to small input
  .button-size(@btn-sm-padding-vertical; @btn-sm-padding-horizontal; @btn-sm-font-size; @btn-sm-line-height; @btn-sm-border-radius);
}
.btn-xs {
  .button-size(@btn-xs-padding-vertical; @btn-xs-padding-horizontal; @btn-xs-font-size; @btn-xs-line-height; @btn-xs-border-radius);
}


// Block button
// --------------------------------------------------

.btn-block {
  display: block;
  width: 100%;
}

// Vertically space out multiple block buttons
.btn-block + .btn-block {
  margin-top: 5px;
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
  &.btn-block {
    width: 100%;
  }
}
