// Button variants
//
// Easily pump out default styles, as well as :hover, :focus, :active,
// and disabled options for all buttons
.button-variant(@color; @background; @border) {
  color: @color;
  background-color: @background;
  border-color: @border;

  &:focus,
  &.focus {
    color: @color;
    background-color: darken(@background, 10%);
        border-color: darken(@border, 25%);
  }
  &:hover {
    color: @color;
    background-color: darken(@background, 10%);
        border-color: darken(@border, 12%);
  }
  &:active,
  &.active,
  .open > .dropdown-toggle& {
    color: @color;
    background-color: darken(@background, 10%);
        border-color: darken(@border, 12%);

    &:hover,
    &:focus,
    &.focus {
      color: @color;
      background-color: darken(@background, 17%);
          border-color: darken(@border, 25%);
    }
  }
  &:active,
  &.active,
  .open > .dropdown-toggle& {
    background-image: none;
  }
  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    &:hover,
    &:focus,
    &.focus {
      background-color: @background;
          border-color: @border;
    }
  }

  .badge {
    color: @background;
    background-color: @color;
  }
}

.button-variant-many(@color; @background; @border; @shadow;
                @hover-color; @hover-backgorund; @hover-border; @hover-shadow;
                @active-color; @active-backgorund; @active-border; @active-shadow;
                @disabled-color; @disabled-backgorund; @disabled-border; @disabled-shadow) {
  color: @color;
  background-color: @background;
  border-color: @border;

  &:focus,
  &.focus, &:hover, .hover {
    color: @hover-color;
    background-color: @hover-backgorund;
    border-color: @hover-border;
    box-shadow: @hover-shadow;
  }
  &:active,
  &.active,
  .open > .dropdown-toggle& {
    color: @active-color;
    background-color: @active-backgorund;
    border-color: @active-border;
    box-shadow: @active-shadow;

    &:hover,
    &:focus,
    &.focus {
      color: @hover-color;
      background-color: @hover-backgorund;
      border-color: @hover-border;
      box-shadow: @hover-shadow;
    }
  }
  &:active,
  &.active,
  .open > .dropdown-toggle& {
    background-image: none;
  }
  &.disabled,
  &[disabled] {
    color: @disabled-color;
    background-color: @disabled-backgorund;
    border-color: @disabled-border;
    box-shadow: @disabled-shadow;
    &:hover,
    &:focus,
    &.focus {
      color: @disabled-color;
      background-color: @disabled-backgorund;
      border-color: @disabled-border;
      box-shadow: @disabled-shadow;
    }
  }

  .badge {
    color: @background;
    background-color: @color;
  }
}

// Button sizes
.button-size(@padding-vertical; @padding-horizontal; @font-size; @line-height; @border-radius) {
  padding: @padding-vertical @padding-horizontal;
  font-size: @font-size;
  height: @line-height;
  line-height: @line-height;
  border-radius: @border-radius;
}
